<template>
    <div class="game-difficulty">
      <div 
        class="difficulty-button" 
        :class="{'active': currentDifficultyLevel == difficultyLevel.EASY}"
        @click="changeDifficultyLevel(difficultyLevel.EASY)"
      >
        <img src="@/assets/star/1.png" alt=""> 
      </div>
      <div 
        class="difficulty-button" 
        :class="{'active': currentDifficultyLevel == difficultyLevel.AVERAGE}"
        @click="changeDifficultyLevel(difficultyLevel.AVERAGE)"
      >
        <img src="@/assets/star/2.png" alt=""> 
      </div>
      <div 
        class="difficulty-button" 
        :class="{'active': currentDifficultyLevel == difficultyLevel.DIFFICULT}"
        @click="changeDifficultyLevel(difficultyLevel.DIFFICULT)"
      >
        <img src="@/assets/star/3.png" alt=""> 
      </div>
      <div class="disabled" v-if="disabled"></div>
    </div>
</template>

<script>
import { DifficultyLevel } from '@/utils/DifficultyLevel';

export default {
  name: 'GameDifficulty',
  props: {
    changeDifficultyLevel: {
        type: Function,
        required: true
    },
    currentDifficultyLevel: {
        type: Number,
        required: true
    },
    disabled: {
        type: Boolean,
        required: true
    },
  },
  computed: {
    difficultyLevel () {
        return DifficultyLevel
    }
  }
}
</script>

<style scoped>
.game-difficulty {
  display: flex;
  gap: 20px;
  align-items: center;
  position: relative;
}

.difficulty-button {
  width: 30px;
  height: 30px;
  border: 1px solid rgba(128, 128, 128, 0);
  border-radius: 50%; 
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer
}

.difficulty-button:hover {
  border: 1px solid gray;
}

.difficulty-button.active {
  border: 1px solid red;
}

.game-difficulty img {
  width: 20px;
  height: 20px;
  object-fit: cover;
}

.disabled {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: white;
  opacity: 0.8;
}
</style>
