<template>
  <div class="game-footer">
    <button class="button start" v-if="!started" @click="start">Старт</button>
    <button class="button stop" v-if="started" @click="stopGame">Стоп</button>
  </div>
</template>

<script>
export default {
  name: 'GameFooter',
  props: {
    started: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    start () {
      setTimeout(() => {
        this.restartGame();
      }, 500);
    },
    restartGame () {
      window.$game.restart();
    },
    stopGame () {
      window.$game.stop()
    }
    
  },
}
</script>

<style scoped>
.game-footer {
  width: 100%;
  padding: 40px;
  background-color: #ffffff;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  overflow: hidden;
}

.button {
  background-color: #ffffff;
  border: none;
  outline: none;
  padding: 10px 40px;
  border-radius: 25px;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
  color: rgb(36, 36, 36);
  border: dashed 2px rgb(36, 36, 36);
  transition: 200ms;
}

.button.start:hover {
  background-color: #66ff00;
}
.button.stop:hover {
  background-color: #fa3909;
}
</style>