  </div>
</template>

<script>

export default {
  name: 'GameOver',
  data () {
    return {
      gameOver: false
    }
  },
  props: {
    isGameOver: {
      type: Boolean,
      required: true
    },
    restartGame: {
      type: Function,
      required: true
    }
  }
}
</script>

<style scoped>
.game-over {
  width: 100%;
  height: 100%;
  background: #0000006c;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.game-over > img {
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.close {
  background-color: #ffffff;
  border: none;
  outline: none;
  padding: 10px 40px;
  border-radius: 25px;
  text-transform: uppercase;
  font-weight: 700;
