<template>
  <BoardContainer>
    <BoardButton :shortkey="'A'" :color="'#FF5643'" :sound="'1.mp3'"/>
    <BoardButton :shortkey="'B'" :color="'#FEEF33'" :sound="'2.mp3'"/>
    <BoardButton :shortkey="'C'" :color="'#1E90FF'" :sound="'3.mp3'"/>
    <BoardButton :shortkey="'D'" :color="'#17DE15'" :sound="'4.mp3'"/>
    <GameOver :isGameOver="isGameOver" :restartGame="restartGame" />
  </BoardContainer>
</template>

<script>
import BoardContainer from './GameButtons/Container.vue'
import BoardButton from './GameButtons/Button.vue'
import GameOver from './GameOver.vue'
import { GameStatus } from '@/utils/GameStatus'

export default {
  name: 'GameBoard',
  components: {
    BoardContainer,
    BoardButton,
    GameOver
  },
  props: {
    store: {
      type: Object,
      required: true
    }
  },
  computed: {
    level: {
      get () {
        return window.$store.level
      },
      set (value) {
        window.$store.level = value
      }
    },    
    currentSequence: {
      get () {
        return window.$store.currentSequence
      },
      set (value) {
        window.$store.currentSequence = value
      }
    },
    status: {
      get () {
        return  window.$store.status
      },
      set (value) {
        window.$store.status = value
      }
    },
    isGameOver () {
      return this.store.status == GameStatus.GAMEOVER
    },
    restartGame () {
      return this.restart
    },
  },
  methods: {
    play (sequence = []) {
      this.setStatus(GameStatus.WAITING);
      sequence.forEach((n, i) => {
        setTimeout(() => {
          document.querySelectorAll(`[data-board-button]`)[n].click();
          if (i == sequence.length - 1) {
            this.setStatus(GameStatus.LISTENING);
          } 
        }, this.store.difficultyLevel * i);
      });
    },
    levelUp () {
      this.level++;
      this.currentSequence = [];
      for (let i=0; i<this.level; i++) {
        this.currentSequence.push(this.randomNumber(0,3));
      }
      this.play(this.currentSequence);
    },
    restart () {
      this.setStatus(GameStatus.WAITING);
      window.$store.started = true;
      window.$store.hits = [];
      window.$store.level = 0;
      window.$store.elapsedTime = 0;
      window.$store.currentSequence = [];
      window.$store.sequenceListener = undefined;       
      setTimeout(() => {
        window.$game.levelUp();
      }, 500);      
    },    
    stop () {
      this.setStatus(GameStatus.WAITING);
      window.$store.started = false;
      window.$store.hits = [];
      window.$store.level = 0;
      window.$store.elapsedTime = 0;
      window.$store.currentSequence = [];
      window.$store.sequenceListener = undefined;       
    },    
    gameOver () {
      this.setStatus(GameStatus.GAMEOVER);
      window.$store.started = false;
    },
    randomNumber (min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min;
    },
    setStatus (status) {
      window.$store.status = status;
    },
    getStatus () {
      return window.$store.status;
    },
    sequence () {
      return this.currentSequence;
    },
    shiftSequence () {
      return this.currentSequence.shift();
    }, 
  },
  mounted () {
    window.$game = this;
  }
}
</script>

