<template>
  <div class="game-header">
      <div class="game-title">Simon Says</div>
      <div class="game-level">lv: <span>{{ level }}</span></div>
      <GameDifficulty 
        :changeDifficultyLevel="changeDifficultyLevel" 
        :currentDifficultyLevel="currentDifficultyLevel"
        :disabled="currentDifficultyDisabled"
      />
  </div>
</template>

<script>
import GameDifficulty from './GameDifficult/DifficultContainer.vue'
export default {
  name: 'GameHeader',
  components: {
    GameDifficulty
  },
  props: {
    store: {
      type: Object,
      required: true
    }
  },
  computed: {
    level () {
      return this.store.level
    },
    currentDifficultyLevel () {
      return this.store.difficultyLevel
    },
    currentDifficultyDisabled () {
      return this.store.started
    }
  },
  methods: {
    changeDifficultyLevel (difficulty) {
      window.$store.difficultyLevel = difficulty
    }
  }
}
</script>

<style scoped>
.game-header {
  width: 100%;
  padding: 40px;
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 8px;
  overflow: hidden;
}
.game-title {
  font-size: larger;
  font-weight: 700;
  color: rgb(36, 36, 36);
}
.game-level {
  font-size: larger;
  font-weight: 700;
  color: rgb(36, 36, 36);
}
.game-level > span {
  font-size: 30px;
  padding-left: 10px;
  color: rgb(13, 13, 255);
}
</style>