<template>
  <div class="container">
    <GameBg />
    <div class="game-page">
      <GameHeader :store="store" />
      <GameBoard :store="store" />
      <GameFooter :started="isStarted" />
    </div>
  </div>
</template>

<script>
import GameBg from './GameBg.vue';
import GameBoard from './GameBoard.vue';
import GameHeader from './GameHeader.vue'
import GameFooter from './GameFooter.vue'
import { GameStatus } from '../utils/GameStatus'
import { DifficultyLevel } from '../utils/DifficultyLevel'

export default {
  name: 'GamePage',
  data () {
    return {
      hits: [],
      level: 0,
      started: false,
      status: GameStatus.WAITING, // waiting, ready, listening, gameover
      difficultyLevel: DifficultyLevel.EASY, // easy - лёгкий, average - средний, difficult - сложный
      elapsedTime: 0,
      currentSequence: [],
      sequenceListener: undefined 
    }
  },
  components: {
    GameBg,
    GameBoard,
    GameHeader,
    GameFooter,
  },

  computed: {
    store () {
      return this.$data
    },
    currentGameStatus () {
      return this.status
    },
    isStarted () {
      return this.started
    }
  },
  mounted () {
    window.$store = this.$data;
  }
}
</script>

<style scoped>
.container {
  padding: 40px;
}
.game-page {
  width: 500px;
  height: 100vh;
  position: relative;
  display: flex; 
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  z-index: 10;
}
</style>
  