<template>
  <div class="game-board-button">
    <button ref="btn" :style="`background-color:${color}`" @click='click($event)' :data-board-button="shortkey"></button>  
  </div>
</template>

<script>
import { GameStatus } from '@/utils/GameStatus';

export default {
  name: 'BoardButton',
  props: {   
    shortkey: {
      type: String,
      required: true
    },
    color: {
      type: String,
      required: true
    },
    sound: {
      type: String,
      required: false
    }
  }, 
  methods: {
    click ($event) {
      console.log('click', window?.$store.status)
      const game_status = window.$store.status;
      if (game_status != GameStatus.LISTENING && (!$event || $event.isTrusted)) {
        return;
      }
      const $btn = this.$refs.btn;
      const key = $btn?.dataset?.boardButton.toLowerCase();
      $btn.classList.add('click');
      setTimeout(() => { $btn.classList.remove('click'); }, 200);
      this.playSound(require(`@/assets/sounds/${this.sound}`));
      this.checkSequence(key);
    },
    checkSequence (key) {
      if (window.$game.getStatus() === GameStatus.LISTENING) {
        const keymap = {"a": 0, "b": 1, "c": 2, "d": 3};
        const keyindex = keymap[key.toLowerCase()];
        const expected = window.$game.shiftSequence();
        if (expected == keyindex) {
          if (!window.$game.sequence().length) {
            setTimeout(() => {
              if (window.$store.status == GameStatus.GAMEOVER) return;
              window.$game.setStatus(GameStatus.WAITING);
              window.$game.levelUp();
            }, 1000);
          }
        } else {
          window.$game.gameOver();
        }
      }
    },
    playSound (sound) {
      const audio = new Audio(sound);
      audio.play();
    } 
  }
}
</script>
  
<style scoped>
.game-board-button {
  width: 100px;
  height: 100px;
}
button {
  width: 100%;
  height: 100%;
  border: none;
  cursor: pointer;
  position: relative;
  opacity: 0.5;
}
button:before {
  content: '';
  background-color: inherit;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform: scale(0.001, 0.001);
}
button.click {
  opacity: 1;
  pointer-events: none;
  user-select: none;
} 
button.click:before {
  outline: none;
  user-selection: none;
  pointer-events: none;        
  animation: effect 800ms;
}
@keyframes effect {
  50% {
    will-change: scale;
    transform: scale(1.3, 1.3);
    opacity: 0;
  }
  99% {
    will-change: scale;
    transform: scale(0.001, 0.001);
    opacity: 0;
  }
  100% {
    will-change: scale;
    transform: scale(0.001, 0.001);
    opacity: 1;
  }
}  
</style>
  